<template>
	<div id="estore-order-item">
		<div class="order-item" @click="util.jsUrl('/plugin/pages/paotui/store/order/detail', {id: order.id})">
			<div class="top van-hairline--bottom">
				<div>
					<span class="serial-sn">#{{order.id}}</span>
					<span class="meituan" v-if="order.data.goods_source == '美团'">
						{{order.data.goods_source}}
						<template v-if="order.data.serial_sn">
							#{{order.data.serial_sn}}
						</template>
					</span>
					<span class="eleme" v-else-if="order.data.goods_source == '饿了么'">
						{{order.data.goods_source}}
						<template v-if="order.data.serial_sn">
							#{{order.data.serial_sn}}
						</template>
					</span>
				</div>
				<div class="flex">
					<span class="mode">{{order.order_type == 'take' ? '帮取' : '帮送'}}</span>
					<span class="comment-status" v-if="0">未评价</span>
					<span class="order-status">{{order.status_cn}}</span>
					<span class="icon icon-right font-12 c-gray"></span>
				</div>
			</div>
			<div class="middle">
				<template v-if="order.order_type == 'take'">
					<div class="address-item from">
						<div class="left">
							<div class="mark">发</div>
						</div>
						<div class="right">
							<div class="title">{{order.buy_address}}</div>
							<div class="desc">
								<span>{{order.buy_username}}</span>
								<span>{{order.buy_sex}}</span>
								<span>{{order.buy_mobile}}</span>
							</div>
						</div>
					</div>
					<div class="address-item to">
						<div class="left">
							<div class="mark">收</div>
						</div>
						<div class="right">
							<div class="title">{{order.accept_username}}</div>
							<div class="desc">{{order.accept_address}}</div>
							<div class="title margin-5-t">{{order.accept_mobile}}</div>
						</div>
					</div>
				</template>
				<template v-else-if="order.order_type == 'send'">
					<div class="address-item from">
						<div class="left">
							<div class="mark">发</div>
						</div>
						<div class="right">
							<div class="title">{{order.buy_username}}</div>
							<div class="desc">{{order.buy_address}}</div>
							<div class="title margin-5-t">{{order.buy_mobile}}</div>
						</div>
					</div>
					<div class="address-item to">
						<div class="left">
							<div class="mark">收</div>
						</div>
						<div class="right">
							<div class="title">{{order.accept_address}}</div>
							<div class="desc">
								<span>{{order.accept_username}}</span>
								<span>{{order.accept_sex}}</span>
								<span>{{order.accept_mobile}}</span>
							</div>
						</div>
					</div>
				</template>
			</div>
			<div class="bottom van-hairline--top">
				<div class="addtime">{{order.addtime_cn}}</div>
				<div>
					<template v-if="order.is_pay == 0">
						<van-button type="danger" size="small"  @click.stop="onChangeStatus('pay')">立即支付</van-button>
					</template>
					<template v-else>
						<template v-if="order.status == 1">
							<van-button type="danger" size="small" @click.stop="onChangeStatus('cancel')">取消订单</van-button>
						</template>
						<template v-else-if="order.status == 2">
							<van-button type="danger" size="small" @click.stop="onChangeStatus('cancel')" v-if="order.delivery_status == 1">取消订单</van-button>
							<van-button type="danger" size="small" @click.stop="onChangeStatus('end')">确认送达</van-button>
						</template>
						<template v-else>
							<van-button type="danger" size="small" @click.stop="util.jsUrl('/plugin/pages/paotui/store/order/diy', {mode: order.order_type, order_id: order.id})">再来一单</van-button>
						</template>
					</template>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			order: Object,
			order_index: Number,
		},
		data() {
			return {
				isRefresh: false
			}
		},
		components: {

		},
		methods: {
			onChangeStatus: function(type) {
				var params = {order: this.order, type: type, index: this.order_index};
				this.$emit('onChangeStatus', params);
			}
		}
	}
</script>

<style>
	#estore-order-item .order-item{
		background-color: #fff;
		padding-left: 15px;
		font-size: 14px;
		margin-bottom: 10px;
	}
	#estore-order-item .order-item .top{
		width: 100%;
		height: 44px;
		padding-right: 15px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	#estore-order-item .order-item .serial-sn{
		font-size: 15px;
		font-weight: 500;
	}
	#estore-order-item .order-item .mode{
		padding: 3px 5px;
		margin-right: 10px;
		font-size: 12px;
		color: #ff2d4b;
		border: 1px solid rgba(255, 45, 75, .2);
		background-color: rgba(255, 45, 75, .1);
		border-radius: 3px;
	}
	#estore-order-item .order-item .comment-status{
		margin-right: 10px;
		color: #ff2d4b;
	}
	#estore-order-item .order-item .order-status{
		color: #999;
		margin-right: 5px;
	}
	#estore-order-item .order-item .middle{
		width: 100%;
		padding-right: 15px;
	}
	#estore-order-item .order-item .address-item{
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin: 15px 0;
	}

	#estore-order-item .order-item .address-item .left{
		width: 10%;
	}
	#estore-order-item .order-item .address-item .right{
		width: 90%;
	}
	#estore-order-item .order-item .address-item .mark{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		font-size: 12px;
		color: #fff;
	}
	#estore-order-item .order-item .address-item.from .mark{
		background-color: #ff2d4b;
	}
	#estore-order-item .order-item .address-item.to .mark{
		background-color: #ECAC5C;
	}
	#estore-order-item .order-item .address-item .title{
		line-height: 1.3;
	}
	#estore-order-item .order-item .address-item .desc{
		line-height: 1.3;
		font-size: 13px;
		color: #999;
		margin-top: 5px;
	}
	#estore-order-item .order-item .bottom{
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10px 15px 10px 0;
	}
	#estore-order-item .order-item .addtime{
		color: #999;
	}
	#estore-order-item .order-item .meituan{
		background-color: #FFB814;
		color: #fff;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 2px;
	}
	#estore-order-item .order-item .eleme{
		background-color: #2395ff;
		color: #fff;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 2px;
	}
</style>


