<template>
	<div id="estore-order">
		<public-header title="我的订单">
			<div slot="right" class="search-btn" @click="util.jsUrl('/plugin/pages/paotui/store/order/search')">
				<span class="icon icon-search font-20"></span>
			</div>
		</public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr">
					<div
						class="tab-item"
						v-for="(item, index) in order_status"
						:class="{active: filter.items.status == item.status}"
						:key="index"
						@click="onToggleStatus(item.status)"
					>
						{{item.text}}
					</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
				>
					<template v-for="(item, index) in records.data">
						<order-item
							:order="item"
							:order_index="index"
							@onChangeStatus="onChangeStatus"
						>
						</order-item>
					</template>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<div class="create">
			<div class="create-item" @click="util.jsUrl('/plugin/pages/paotui/store/order/diy', {mode: 'send'})">帮我送</div>
			<div class="create-item" @click="util.jsUrl('/plugin/pages/paotui/store/order/diy', {mode: 'take'})">帮我取</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import orderItem from "../components/orderItem"
	import {getOrderList, changeOrderStatus} from "../controller/order"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				order_status: [
					{
						status: 1,
						text: '待接单',
					},
					{
						status: 2,
						text: '进行中',
					},
					{
						status: 3,
						text: '已完成',
					},
					{
						status: 4,
						text: '已取消',
					},
					{
						status: 0,
						text: '全部',
					}
				],
				showPreLoading: true,
				isRefresh: false,
				filter: {
					items: {
						status: 1
					}
				},
				menufooter: {}
			}
		},
		components: {
			publicHeader, publicFooter, orderItem
		},
		methods: {
			onLoad(force){
				var that = this;
				getOrderList({
					vue: that,
					force: force,
					data: {
						filter: that.filter ? that.filter.items: {},
						menufooter: 1
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},
			onChangeStatus(options) {
				options.vue = this;
				changeOrderStatus(options);
				return;
				var oid = params.order.id;
				var type = params.type;
				if(type == 'cancel') {
					var params = {
						url: 'errander/storeOrder/cancel?id=' + oid,
						confirm: '确定取消订单吗?',
					}
				} else if(type == 'end') {
					var params = {
						url: 'errander/storeOrder/end?id=' + oid,
						confirm: '确定已收到商品吗?',
					}
				} else if(type == 'pay') {
					var params = {
						url: 'errander/storeOrder/pay?id=' + oid,
						confirm: '确定去支付吗?',
					}
				}
				this.util.jspost(params);
			},
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#estore-order .create{
		position: fixed;
		left: 50%;
		bottom: 70px;
		transform: translateX(-50%);
		display: flex;
		align-items: center;
		padding: 15px 5px;
		background-color: #ff2d4b;
		color: #fff;
		font-size: 14px;
		border-radius: 100px;
	}
	#estore-order .create .create-item{
		padding: 0 20px;
		font-weight: bold;
	}
	#estore-order .create .create-item:first-child{
		border-right: 1px solid #fff;
	}
	#estore-order .wrap-search~.van-pull-refresh{
		margin-top: 45px;
		border-top: 1px solid #f5f5f5;
	}
</style>


