import util from '@/config/util.js'
import router from '@/router'
import {setData} from "@/controller/funcCommon.js"

function getOrderList(params) {
	if(params.force) {
		params.vue.records = {
			page: 1,
			psize: 15,
			loading: true,
			finished: false,
			empty: false,
			data:[]
		};
	}
	if(params.vue.records.finished) {
		return false;
	}
	params.vue.util.request({
		url: 'errander/storeOrder/index',
		data: {
			page: params.vue.records.page,
			psize: params.vue.records.psize,
			filter: params.data.filter,
			menufooter: params.data.menufooter,
		}
	}).then((res) => {
		let result = res.data.message;
		params.vue.showPreLoading = false;
		if(result.errno) {
			params.vue.util.$toast(result.message);
			return false;
		}
		var records = result.message.records;
		params.vue.records.data = [...params.vue.records.data, ...records];
		if(records.length < params.vue.records.psize ) {
			params.vue.records.finished = true;
			if(!params.vue.records.data.length) {
				params.vue.records.empty = true;
			}
		}
		params.vue.records.page++;
		params.vue.records.loading = false;
		params.vue.isRefresh = false;
		if(params.data.menufooter == 1) {
			params.vue.menufooter = window.menufooter;
		}
	})
};

function orderStatusChangeTips(type) {
	var types = {
		pay: {
			tip: '确定去支付吗?',
			success_status: 1,
			url: 'errander/storeOrder/pay'
		},
		end: {
			tip: '确定已收到商品吗?',
			success_status: 3,
			url: 'errander/storeOrder/end'
		},
		cancel: {
			tip: '确定取消订单吗?',
			success_status: 4,
			url: 'errander/storeOrder/cancel'
		}
	}
	return types[type];
};

function changeOrderStatus(options) {
	console.log(1111111, options);
	var order = options.order;
	var type = options.type;
	var typeInfo = orderStatusChangeTips(type);
	var tip = typeInfo.tip;
	var success_status = typeInfo.success_status;
	var url = typeInfo.url;
	var params = {
		url: url,
		data: {
			id: order.id,
			type: type
		},
		confirm: tip,
		success: function(message) {
			util.$toast(message);
			if(options.from == 'detail' && success_status) {
				options.vue.order.status = success_status;
				options.vue.order = Object.assign({}, options.vue.order);
			} else {
				var types = ['cancel', 'end'];
				if(types.indexOf(type) != -1 && options.index >= 0) {
					if(options.from == 'search') {
						options.vue.records.data[options.index].status = success_status;
					} else {
						options.vue.records.data.splice(options.index, 1);
					}
				}
			}
		}
	};
	util.jspost(params);
};

export {
	getOrderList,
	orderStatusChangeTips,
	changeOrderStatus
}



